import React from "react";
import { useDispatch, useSelector } from "react-redux";
import ThemeSwitch from "./ThemeSwitch";
import { selectThemeName, setThemeName } from "../../../store/themeSlice";

const ThemeSwitchContainer = () => {
  const themeName = useSelector(selectThemeName);
  const dispatch = useDispatch();

  const isActive = themeName === "dark";

  const handleClick = () => {
    const newThemeName = themeName === "light" ? "dark" : "light";
    dispatch(setThemeName(newThemeName));
  };

  return <ThemeSwitch onClick={handleClick} isActive={isActive} />;
};

export default ThemeSwitchContainer;
