import { createSelector, createSlice } from "@reduxjs/toolkit";

const initialState = {
  headings: [
    {
      pathname: "/",
      heading: "",
    },
    {
      pathname: "/cpu",
      heading: "",
    },
    {
      pathname: "/bus",
      heading: "",
    },
  ],
  chapters: [
    {
      pathname: "/",
      heading: "",
    },
  ],
};

const screensSlice = createSlice({
  name: "screens",
  initialState,
  reducers: {},
});

export const selectHeadings = (state) => state.screens.headings;
export const selectChapters = createSelector(
  (state) => state.screens.chapters,
  (chapters) =>
    chapters.map(({ heading, pathname }) => {
      const hash = `#${heading
        .toLowerCase()
        .replaceAll(/\s+/g, "_")
        .replaceAll("?", "")}`;

      return {
        pathname,
        heading,
        hash,
      };
    })
);
export const selectChaptersByPathname = (pathname) =>
  createSelector(selectChapters, (chapters) =>
    chapters.filter(
      ({ pathname: chapterPathname }) => pathname === chapterPathname
    )
  );

export default screensSlice.reducer;
