import React from "react";
import PropTypes from "prop-types";
import NavigationLink from "./navigation_link";
import styles from "./ScreensNavigation.module.scss";

const ScreensNavigation = ({ headings }) => (
  <nav className={styles.nav}>
    {headings.map(({ pathname, heading }) => (
      <NavigationLink key={pathname} to={pathname}>
        {heading}
      </NavigationLink>
    ))}
  </nav>
);

ScreensNavigation.propTypes = {
  headings: PropTypes.arrayOf(
    PropTypes.shape({
      pathname: PropTypes.string,
      heading: PropTypes.string,
    })
  ).isRequired,
};

export default ScreensNavigation;
