import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import Chapter from "./Chapter";
import { selectChaptersByPathname } from "../../store/screensSlice";

const ChapterContainer = ({ children }) => {
  const { pathname } = useLocation();
  const [id, setId] = useState("");
  const chapters = useSelector(selectChaptersByPathname(pathname));

  useEffect(() => {
    const chapter = chapters.find(({ heading }) => heading === children);
    if (chapter !== undefined) setId(chapter.hash.replace("#", ""));
  }, [chapters, children]);

  return <Chapter id={id}>{children}</Chapter>;
};

ChapterContainer.propTypes = {
  children: PropTypes.string.isRequired,
};

export default ChapterContainer;
