import React from "react";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import ChaptersNavigation from "./ChaptersNavigation";
import { selectChaptersByPathname } from "../../store/screensSlice";

const ChaptersNavigationContainer = () => {
  const { pathname } = useLocation();
  const chapters = useSelector(selectChaptersByPathname(pathname));
  return <ChaptersNavigation chapters={chapters} />;
};

export default ChaptersNavigationContainer;
