import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  inputValue: "",
  autocompleteIsShow: false,
};

const searchSlice = createSlice({
  name: "search",
  initialState,
  reducers: {
    setInputValue: (state, { payload }) => {
      state.inputValue = payload;
    },
    setAutocompleteIsShow: (state, { payload }) => {
      state.autocompleteIsShow = payload;
    },
  },
});

export const { setInputValue, setAutocompleteIsShow } = searchSlice.actions;

export const selectInputValue = (state) => state.search.inputValue;
export const selectAutocompleteIsShow = (state) =>
  state.search.autocompleteIsShow;

export default searchSlice.reducer;
