import React from "react";
import PropTypes from "prop-types";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import Autocomplete from "./Autocomplete";
import { selectChaptersByPathname } from "../../../../store/screensSlice";
import { selectAutocompleteIsShow } from "../../../../store/searchSlice";

const AutocompleteContainer = ({ value }) => {
  const { pathname } = useLocation();
  const chapters = useSelector(selectChaptersByPathname(pathname));
  const show = useSelector(selectAutocompleteIsShow);

  const matches =
    value.length > 0 && show
      ? chapters.filter(({ heading }) =>
          heading.toLowerCase().startsWith(value.toLowerCase())
        )
      : [];

  return <Autocomplete items={matches} show={show} />;
};

AutocompleteContainer.propTypes = {
  value: PropTypes.string.isRequired,
};

export default AutocompleteContainer;
